import React, {useState,useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CardEditMenu from './CardEditMenu.js'
import Avatar from '@material-ui/core/Avatar';
import SearchBox from 'components/SearchBox/index';
import {base_url, sticker_url, $http} from "config";
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import Fab from '@material-ui/core/Fab';
import SweetAlert from 'react-bootstrap-sweetalert';

const columnData = [
    {id: "idx", align: false, disablePadding: true, label: "ID"},
    {id: "image", align: false, disablePadding: true, label: "Image"},
    {id: "name", align: true, disablePadding: false, label: "Image Name"},
    {id: "status", align: true, disablePadding: false, label: "Status"},
    {id: "Actions", align: true, disablePadding: false, numeric:"right", label: "Actions"}
];

const DataTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                  <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={() => createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

DataTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
};


let DataTableToolbar = props => {

  const {NewImage, category} = props;
  return (
    <Toolbar className="table-header">
      <div className="title">
          <Typography variant="h6">{category}</Typography>
      </div>
      <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
          <SearchBox placeholder="Search ..."/>
      </div>
      <div className="actions">
        <Tooltip title="Add Sticker" onClick={()=>NewImage()}>
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-collection-image"/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};


const CategoryInfoTable = (props) => {

  const {category_id} = props;
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("calories");
  const [selected, setSelected] = useState([]);
  const [stikerdata, setData] = useState([]);
  const [open, setOpen] = useState(false);
  const [loader, setLoader] = useState(false);
  const [add_img, setAddImg] = useState(null);
  const [img_name, setImgName] = useState("");
  const [ImageUrl, setImageUrl] = useState('https://via.placeholder.com/300x300');
  const [category, setCategory] = useState(0);
  const [status, setStatus] = useState(0);
  const [sticker_id, setStickerId] = useState(0);
  const {width} = useSelector(({settings}) => settings);
  const [goback, setGoback] = useState(false)

  const status_rows = ([
    {id: 1, content: 'Active'},
    {id: 0, content: 'Disable'}
  ])

  useEffect(() => {
    $http.get(base_url + "category/getCategoryInfo", {params : {id: category_id}})
    .then((response) => {
      
      setData(response.data.stickers);
      setCategory(response.data.category.name);
    }).catch((error) => {
      setGoback(true)
    })
    setLoader(true);
    setStickerId(0);
  }, [category_id]);

  const init_Data = () => {
    $http.get(base_url + "category/getCategoryInfo", {params : {id: category_id}})
    .then((response) => {
      setData(response.data.stickers);
      setCategory(response.data.category.name);
    }).catch((error) => {
      setGoback(true)
    })
    setLoader(true);
    setStickerId(0);
  }

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const options = ([
    {content: 'Modify', id: 1},
    {content: 'Delete', id: 2}
  ]);

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    sort_order === "desc"
      ? stikerdata.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : stikerdata.sort((a, b) => (a[property] < b[property] ? -1 : 1));
    
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleKeyDown = (event, id) => {
    if (keycode(event) === "space") {
      handleClick(event, id);
    }
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };

  const onSelectCategory = (data, index) => {
    if (index === 0) {
      } else if (index === 1) {
        openImageModal(data);
      } else if (index === 2) {
        const frmData = new FormData();
        frmData.append('id', data);
        $http.post(base_url + "sticker/deleteSticker",frmData)
        .then((response) => {
          if (response.data.status === "success") {
              NotificationManager.info("You removed selected sticker.", 1000, () => {
              });
              if(navigator.onLine)
                init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 1000, () => {
              });
          }
         
        }).catch((error)=>{
          NotificationManager.error(error, "Error!", 1000, () => {
          });
         })
        if(!navigator.onLine){
          caches.open('SirvezApp').then(function(cache){

            cache.match('/api/category/getCategoryInfo?id='+category_id)
            .then(function(response) {return response ? response.json() : null})
            .then(function(responseData){
              if(responseData ==null) return
              for (var i = 0; i < responseData.stickers.length; i++) {
                if (responseData.stickers[i].id == data) {
                  responseData.stickers.splice(i,1)
                  const jsonResponse = new Response(JSON.stringify(responseData), {
                    headers: {
                        'content-type': 'application/json'
                    }
                  });
                  cache.put('/api/category/getCategoryInfo?id='+category_id, jsonResponse.clone())
                  .then(function(result){
                    init_Data()
                  })
                  break;
                }
              }
            })
            
            cache.match('/api/room/editPhoto')
            .then(function(response) {return response ? response.json() : null})
            .then(function(responseData){
              if(responseData == null) return
              for (var i = 0; i < responseData.categories.length; i++) {
                if (responseData.category[i].id == category_id) {
                  for(var j = 0; j <responseData.category[i].stickers.length;j++){
                    responseData.category[i].stickers.splice(j,1)
                    const jsonResponse = new Response(JSON.stringify(responseData), {
                      headers: {
                          'content-type': 'application/json'
                      }
                    });
                    cache.put('/api/room/editPhoto', jsonResponse.clone())
                    
                    break;
                  }
                }
              }
            })
            
          })
        }
      }
  }

  const openImageModal = (data) => {
    if (data) {
      $http.get(base_url + "sticker/getStickerInfo", {params: {id: data}})
      .then((response) => {
        setStickerId(data);
        setImgName(response.data.stiker.name);
        setStatus(response.data.stiker.status);
        setImageUrl(response.data.stiker.stiker_img ? 
          (sticker_url + category + '/' + response.data.stiker.stiker_img) : 
          'https://via.placeholder.com/300x300');

      }).catch((error) => {
        setGoback(true)
      })
    } else {
      setImageUrl('https://via.placeholder.com/300x300');
      setStickerId(0);
    }
    setOpen(true);
  }
  const handleCloseClick = () => {
      setOpen(false);
  }
  const AddImageClick = () => {
    handleCloseClick();

    let offline_id = sticker_id > 0 ? sticker_id:JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
    const frmData = new FormData();
    frmData.append('id', offline_id);
    frmData.append('stiker_img', add_img);
    frmData.append('name', img_name);
    frmData.append('category_id', category_id);
    frmData.append('category', category);
    frmData.append('status', status);
    $http.post(base_url + "sticker/updateSticker", frmData)
    .then((response) => {
        if (response.data.status === "success") {
            NotificationManager.info("Sticker updated!", "Success!", 1000, () => {
            });
            if(navigator.onLine)
              init_Data();
              
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
   })
    if (!navigator.onLine){
      caches.open('SirvezApp').then(function(cache){
          let fileName = ''
          if(add_img)
          {
              fileName = new Date().getTime()+'_new.jpeg';
              cache.put('/pixie/assets/images/stickers/'+ category + '/'+fileName, new Response(add_img));
          }
          var modify_data = {
              id : offline_id,
              name : img_name,
              category_id : category_id,
              category : category,
              stiker_img : fileName,
              
          }   
          //save category_edit
          
            let res_data = {stiker:modify_data}
            const j_Response = new Response(JSON.stringify(res_data), {
                headers: {
                    'content-type': 'application/json'
                }
            });
            cache.put('/api/category/sticker/getStickerInfo?id='+offline_id, j_Response.clone());
          
            
          //save into list
          const requestUrl = '/api/category/getCategoryInfo?id='+category_id;
          cache.match(requestUrl)
          .then(function(response) {return response.json()})
          .then(function(respon_data){
              let item = respon_data.stickers.find((item) => {return (item.id === offline_id)})
              if (item) {
                  item.name = img_name
                  item.category = category
                  if(fileName!='')
                    item.stiker_img = fileName
              }
              else
              {
                respon_data.stickers.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(respon_data), {
                  headers: {'content-type': 'application/json'}
              });
              cache.put(requestUrl, jsonResponse.clone())
          })

          //add-api/room/editPhoto
          cache.match('/api/room/editPhoto')
          .then(function(response) {return response ? response.json() : null})
          .then(function(res_data){
            if(res_data == null) return
              let cat_item = res_data.categories.find((item) => {return (item.id === category_id)})
              if(cat_item){
                let item = cat_item.stickers.find((item) => {return (item.id === offline_id)})
                if (item) {
                    item.name = img_name
                    item.category = category                  
                    if(fileName!='')
                      item.stiker_img = fileName
                }
                else
                {
                  cat_item.stickers.unshift(modify_data)
                }
                const jsonResponse = new Response(JSON.stringify(res_data), {
                    headers: {'content-type': 'application/json'}
                });
                cache.put('/api/room/editPhoto', jsonResponse.clone())
                .then(function(response){
                  init_Data();
                })
              }
          })
          
      }).catch((err)=>{
          console.log('cashe_error',err);
      })
    }
  }

  const changeImage = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setAddImg(file);
        setImageUrl(reader.result);
    }
    reader.readAsDataURL(file)
  }

  return (
    loader ? (
      <div>
      <DataTableToolbar NewImage={() => openImageModal(0)} category={category} />
      <div className="flex-auto">
        <div className="table-responsive-material">
          <Table className="">
            <DataTableHead
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {stikerdata.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(n => {
               
                return (
                  <TableRow
                    hover
                    // onClick={event => handleClick(event, n.id)}
                    onKeyDown={event => handleKeyDown(event, n.id)}
                    tabIndex={-1}
                    key={n.id}
                  >
                    <TableCell>{n.id}</TableCell>
                    <TableCell>
                        <div className="user-profile d-flex flex-row align-items-center">
                            <Avatar
                                alt={n.name}
                                src={sticker_url + category + '/' + n.stiker_img }
                                
                                className="user-avatar"
                            />
                        </div>
                    </TableCell>
                    <TableCell>{n.name}</TableCell>
                    <TableCell>{n.status > 0 ? 'Active' : 'Deactive'}</TableCell>
                    <TableCell className="text-right">
                        <CardEditMenu key={n.id} selectedData={n.id} onSelectRow={onSelectCategory} menu_rows={options}/>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  count={stikerdata.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onChangePage={handleChangePage}
                  onChangeRowsPerPage={handleChangeRowsPerPage}
                />
              </TableRow>
            </TableFooter>
          </Table>
        </div>
      </div>
      <Modal key="add-img" className="modal-box" toggle={handleCloseClick} isOpen={open}>
          <ModalHeader>Add Image
            <IconButton className="text-grey"
                        onClick={handleCloseClick}>
                <CloseIcon/>
            </IconButton>
          </ModalHeader>
          <div className="modal-box-content">
            <div className="row">
              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                <div className="jr-card pb-2">
                    <div className="jr-card-thumb">
                        <img className="card-img-top img-fluid" alt="products" src={ImageUrl}/>
                    </div>
                    <input type="file" id="add_img" accept=".svg" 
                        name="add_img" style={{display: "none"}}
                        onChange={(event) => changeImage(event)}/>

                    <div className="jr-card-social text-right">
                        <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                            onClick={(e)=>{document.getElementById('add_img').click()}}>
                            <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                        </Fab>
                    </div>
                </div>
              </div>
              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                <TextField
                    autoFocus
                    margin="dense"
                    id="category"
                    value={category}
                    label="Category Name"
                    helperText="Please Enter Category"
                    fullWidth
                    disabled
                />
                <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    value={img_name}
                    onChange={(event)=>setImgName(event.target.value)}
                    label="Name"
                    helperText="Please Enter Name"
                    fullWidth
                />
                <TextField
                    id="status"
                    select
                    label="Status"
                    value={status}
                    onChange={(event)=>setStatus(event.target.value)}
                    SelectProps={{}}
                    helperText="Please select Status"
                    fullWidth
                >
                    {status_rows.map(option => (
                        <MenuItem key={option.id} value={option.id}>
                            {option.content}
                        </MenuItem>
                    ))}
                </TextField>
            </div>
            </div>
          </div>
          <div className="modal-box-footer d-flex flex-row">
            <Button onClick={handleCloseClick} color="secondary">
              Cancel
            </Button>
            <Button onClick={AddImageClick} color="primary">
              Save
            </Button>
          </div>
        </Modal>
        <NotificationContainer/>
    </div>

    ) : (
      <div className="loader-view"
            style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
        <CircularProgress/>
        <SweetAlert show={goback}
            warning
            confirmBtnText="Go Back"
            confirmBtnBsStyle="danger"
            cancelBtnBsStyle="default"
            title="Warning!"
            onConfirm={() => History.go(-1)}
        >                
        Data not cached, so you can not view this page.
        </SweetAlert>
      </div>        

    )
  );
};

export default CategoryInfoTable;
